@import url(https://fonts.googleapis.com/css?family=Alata&display=swap);
nav.aside-bar {
    width: 30%;
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: #28344C;
    height: 100%;
    text-align: center;
    position: absolute;
    left: 0;
    -webkit-transition: all .5s;
    transition: all .5s;
    box-shadow: 5px 0px 10px 0px rgba(13,13,13,.5);
    z-index: 3;
}

@media (max-width: 1000px) {
    nav.aside-bar {
        width: 40%;
    }
}

@media (max-width: 700px) {
    nav.aside-bar {
        width: 50%;
    }
}

@media (max-width: 600px) {
    nav.aside-bar {
        width: 80%;
    }
}

@media (max-width: 400px) {
    nav.aside-bar header h2 {
        font-size: 20px;
    }
}

.hide-aside-bar {
    left: calc(-100vw) !important;
}

nav.aside-bar * {
    margin: 10px 0;
}

nav.aside-bar header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: relative;
}

nav.aside-bar header img {
    width: 15px;
    height: 15px;
    position: absolute;
    margin: 0;
    right: 0;
}

nav.aside-bar header img:hover {
    cursor: pointer;
}

nav.aside-bar header h2 {
    margin: auto;
    color: #F2C8C4;
}

nav.aside-bar form {
    display: flex;
    flex-direction: column;
}

nav.aside-bar form input, textarea {
    padding: 10px;
    border-radius: 10px;
    border: 0;
    background-color: #F2C8C4;
    color: #0D0D0D;
    border: 1px solid #412f26;
}

nav.aside-bar form textarea.description-todo {
    text-align: start;
    height: 100px;
    resize: none;
}

nav.aside-bar form input.save-todo {
    background-color: #735646;
    text-transform: uppercase;
    font-weight: bold;
    color: #F2C8C4;
    -webkit-transition: all .2s;
    transition: all .2s;
    border: 1px solid #412f26;
}

nav.aside-bar form input.save-todo:hover {
    background-color: #644c3f;
    cursor: pointer;
}
header.header-main {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 20px;
    position: relative;
}

header.header-main h1 {
    margin: auto;
    text-transform: uppercase;
    font-family: 'Alata', sans-serif;
}

header.header-main .add-todo {
    position: absolute;
    left: 0;
    margin-left: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    padding: 5px;
    border: 0;
    border-radius: 10px;
    background-color: #735646;
    -webkit-transition: all .2s;
    transition: all .2s;
}

header.header-main .add-todo:hover {
    cursor: pointer;
    background-color: #644c3f;
}

header.header-main .add-todo * {
    margin: 5px
}

header.header-main .add-todo p {
    color: #F2C8C4;
    font-size: 16px;
    font-weight: bold;
}

header.header-main .add-todo img {
    width: 20px;
}

@media (max-width: 900px) {
    header.header-main .add-todo p {
        display: none;
    }

    header.header-main .add-todo {
        margin: 0 10px;
        position: relative;
        padding: 0;
    }

    header.header-main .add-todo img {
        width: 18px
    }

    header.header-main h1 { 
        margin: 0 10px;
        font-size: 28px;
    }
}
.todo {
    width: 200px;
    min-height: 200px;
    background-color: #283852;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: #F2C8C4;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    padding: 10px
  }

  .todo * {
    color: #F2C8C4;
    text-align: center;
    margin: 2px;
  }
  
  .todo .todo-ready button {
    background-color: #735646;
    color: #F2C8C4;
    padding: 10px;
    border: 1px solid #412f26;
    border-radius: 5px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 10px;
    -webkit-transition: all .2s;
    transition: all .2s;
  }
  
  .todo .todo-ready button:hover {
    background-color: #644c3f;
    cursor: pointer;
  }
  
  .todo .todo-description {
    font-size: 14px;
  }
.todo-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
    align-items: stretch;
    padding-bottom: 20px;
}

.todo-container * {
    margin: 10px;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #0D0D0D;
}

html, body {
    background-color: #F2C8C4;
}

body {
    position: relative;
    min-height: 100vh;
}
